body {
  margin: 0;
  font-family: sans-serif;
}

body {
  background-color: #000;
}

.logo {
  width: 40vw;
  height: 40vw;
}

img {
  width: 100%;
  height: 100%;
}

.Panel {
  width: 95vw;
  color: white;
  padding: 2.5vw;
}

.PanelContent {
  background-color: #333;
  padding: 3vw 5vw 3vw 5vw;
  border-radius: 1rem;
}

.PanelContent > h1 {
  margin: 0;
  font-size: 3.6rem;
}

.PanelContent > table {
  width: 100%;
  table-layout: fixed;
}

th {
  text-align: left;
  font-size: 1.5rem;
}

td {
  font-size: 1.35rem;
}
